/* HEADER CONTAINER */
.Billing-page-header {
  padding: 20px;
  margin-bottom: -10px;
}

/*SECEND-CONTAINER  */
.Billing-page-secend-container {
  display: flex;
  border-top: solid 1px #ccc;
  padding: 20px;
}

/* Button */
/* .Download-btn {
  padding: 20px 10px;
} */

/* TABLE */
.customer-list-table-container {
  border: 1px solid #ccc;
  padding: 20px;
}

.table-customer-list {
  border: 2px solid #ccc;
  border-radius: 10px;
  height: 400px;
  width: 100%;
  overflow: scroll;
}

/* .form-container .Title-Name {
  font-size: 20px;
  padding-left: 20px;
  font-weight: 500;
} */

.form-container.form-container-billing {
  width: 100% !important;
  overflow-x: hidden;
  display: flex;
  border-radius: 0px;
  flex-direction: column;
  color: #000;
  zoom: 90%;
}

.detail-container-main-customer {
  border-top: 1px solid #ccc;
  padding: 20px;
}

.form-container .Billing-form::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  overflow-x: hidden;
}

/* .form-container .Title-Name {
  position: relative;
} */

