  content: "";
  position: absolute;
  bottom: -10px;
  left: 10px;
  width: 8%;
  border-radius: 20px;
  height: 3px;
  background-color: #457cdc;
} */

.form-container .Billing-form::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.form-container .Billing-form::-webkit-scrollbar-thumb {
  background-color: rgb(217, 212, 212);
  outline: 1px solid slategrey;
}

@media screen and (min-width: 769px) {
  .side-bar-menu-close .form-container .Billing-form {
    width: 100vw;
  }
}

.form-container .Billing-form {
  overflow: scroll;
  /* height: 75vh; */
  width: 81vw;
  display: flex;
  flex-direction: column;
  color: #000;
  /* background-color: var(--glass); */
  transition: all 0.3s ease;
}

@media screen and (max-width: 768px) {
  .form-container .Billing-form {
    width: 100%;
  }
}

.input-field .input-mobile {
  width: 100% !important;
  margin-top: 0px !important;

}


.Billing-secend-right {
  border: 2px solid #ccc;
  border-radius: 10px;
  background-color: var(--glass);
  padding: 30px 15px 30px 12px;
  margin-left: 13px;
  width: 38vw;
  height: fit-content;
  /* margin-top: 30px; */
}

.input-feild-booking {
  flex-wrap: wrap;
  margin-bottom: 20px;
  /* gap: 0px; */
}

.Billing-secend-left {
  width: 56%;
}

.input-billing {
  width: 200px !important;
}

.inputfeild-billing-right {
  /* justify-content: center; */
  justify-content: space-evenly;
}

.inputfeild-billing-right-margin-bottom {
  margin-bottom: 30px;
}

@media screen and (min-width:1032px) and (max-width:1240.99px) {
  .Billing-secend-right {
    width: 40%;
  }
}

@media screen and (min-width:967.99px) and (max-width:1031.99px) {
  .Billing-page-secend-container {
    flex-wrap: wrap;
  }

  .inputfeild-billing-right {
    justify-content: center;
  }

  .Billing-secend-left {
    width: 90%;

  }

  .Billing-secend-right {
    width: 90%;
  }

  .input-feild-booking {
    flex-wrap: wrap;
    justify-content: center;
  }

}

@media screen and (max-width:968px) {
  .Billing-page-secend-container {
    flex-wrap: wrap;
  }

  .Billing-secend-left {
    width: 80%;
  }
}

@media screen and (max-width:1033px) {
  .Billing-page-secend-container .Billing-secend-right {
    margin-top: 20px;
  }
}

@media screen and (min-width: 769px) and (max-width: 968px) {
  .Billing-secend-right {
    width: 80%;
  }
}

@media screen and (max-width: 768px) {
  .Billing-secend-right {
    width: 100%;
    margin-left: 0px;
  }
}


@media screen and (max-width:600px) {
  .inputfeild-billing-right {
    flex-wrap: wrap;
    justify-content: center;
  }

  .Billing-secend-left {
    width: 100%;

  }

  .input-feild-left-bottom {
    flex-wrap: wrap;
    justify-content: center;
  }

}

@media screen and (max-width:871.99px) {
  .input-feild-booking {
    flex-wrap: wrap;
    justify-content: center;
  }

  .Billing-page-secend-container {
    justify-content: center;
  }
}

.input-field .input-mobile {
  width: 100% !important;
}

.mobile-division-two {
  flex-wrap: wrap;
  background-color: #fff;
  gap: 1px;
  padding: 20px;
  border-radius: 10px;
  margin: 20px 0px;
}

@media screen and (max-width:500px) {
  .desktop-division-one {
    display: none !important;
  }

  .mobile-division-two {
    display: block !important;

  }
}

@media screen and (min-width:501px) {
  .desktop-division-one {
    display: block !important;
    margin: 10px 0px;
  }

  .mobile-division-two {
    display: none !important;

  }
}

