.biling-amount-input-total {
  padding-top: 10px;
}

.billing-calculation-icon {
  margin-left: 50%;
  margin-top: 5px;
}

.billing-permit-input,
.billing-permit-input,
.billing-toll-input,
.billing-Vpermit-input,
.billing-vendortoll-input {
  padding-top: 10px;
}

@media screen and (max-width: 768px) {
  .Billing-page-header .input {
    margin-top: 0px !important;
  }
}

.billing-inputfield-last {
  flex-wrap: wrap;
  gap: 35px;
}

.vehicle-type-input-field {
  margin-bottom: 14px;
}

@media screen and (max-width: 768px) {
  .billing-inputfield-last .input {
    margin-top: -10px;
  }

  .billing-inputfield-last {
    margin: 20px 0px;
  }

  .Billing-secend-left .input {
    margin-top: 5px;
  }
}



.main-content-billing {
  
  zoom: 90%;
}

@media screen and (min-width: 1300px) and (max-width: 1424.99px) {
  .main-content-billing {
    zoom: 82%;
  }
}

@media screen and (min-width: 1200px) and (max-width: 1299.99px) {
