.SMSFormat-form {
    width: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    color: #000;
    height: fit-content;
}

.SMSFormat-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    /* background: var(--glass); */
    padding-bottom: 30px;
}

.input-field .inputs {
    margin-top: 20px;
}

.input-field .inputs .sms-box {
    background-color: var(--glass);
}

@media screen and (max-width: 650px) {
    .input-field .inputs {
        width: 100% !important;
    }

    .input-field .inputs .sms-box {
        width: 100% !important;
    }
}

