/* TABLE */
.FuelRate-table-container {
    margin-top: 10px;
    border: 1px solid #ccc;
    padding: 20px;
    background-color: var(--glass);
}

.table-FuelRate {
    border: 2px solid #ccc;
    border-radius: 10px;
    height: 400px;
    width: 100%;
}

