.download-container .download-btn {
    position: relative;
}

.download-container .loading::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #3498db;
    border-top: 3px solid transparent;
    animation: spin 0.8s linear infinite;
}

.inputfeild-group-billing {
    flex-wrap: wrap;
    margin-bottom: 5px;
}

@media screen and (min-width: 1557px) {
    .inputfeild-group-billing {
        width: 94%;
    }
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

@media screen and (max-width:1164px) {
    .inputfeild-group-billing {
        justify-content: center;
    }
}

