/* TABLE */
.customer-list-table-container {
    border: 1px solid #ccc;
    padding: 20px;
    background-color: var(--glass);
}

.table-customer-list {
    border: 2px solid #ccc;
    border-radius: 10px;
    height: 400px;
    width: 100%;
    overflow: scroll;
}

/* .form-container-FuelInfo .Title-Name {
    font-size: 20px;
    padding-left: 20px;
    font-weight: 500;
} */

.form-container-FuelInfo {
    width: 100%;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    color: #000;
    height: fit-content;
}

.detail-container-main-customer {
    border-top: 1px solid #ccc;
    background-color: var(--glass);
    padding: 20px;
}

.form-container-FuelInfo .MailageDetails-form::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    overflow-x: hidden;
}

/* .form-container-FuelInfo .Title-Name::after {
