    position: absolute;
    bottom: -10px;
    left: 10px;
    width: 100%;
    border-radius: 20px;
    height: 3px;
    background-color: #457cdc;
} */

.form-container-FuelInfo .MailageDetails-form::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.form-container-FuelInfo .MailageDetails-form::-webkit-scrollbar-thumb {
    background-color: rgb(217, 212, 212);
    outline: 1px solid slategrey;
}

.form-container-FuelInfo .MailageDetails-form {
    border: 2px solid #ccc;
    overflow: scroll;
    height: 75vh;
    width: 81vw;
    background-color: var(--glass);
    display: flex;
    flex-direction: column;
    padding-top: 28px;
    border-radius: 20px;
    color: #000;
}

.mailage-icone {
    padding: 10px 10px 0px;
    color: #616161;
}

.detailsFuel {
    background-color: var(--glass);
    border: 2px solid #ccc;
    border-radius: 10px;
    padding: 15px 15px 25px;
}

