.detailsFuel-inputs {
    flex-wrap: wrap;
    gap: 20px;
}

@media screen and (max-width:1333px) {
    .detailsFuel-inputs {
        flex-wrap: wrap;
        justify-content: center;
    }
}

@media screen and (max-width:768px) {
    .FuelInfo-main {
        width: 97% !important;
    }
}

