.vehicle-tab-main {
    padding-top: 20px;
    display: flex;
    
}

.vehicle-tab-left-main {
    width: 23%;
}

.vehicle-tab-right-main {
    width: 81%;
    padding: 20px;
    height: 80vh;
    overflow: auto;
}


@media screen and (max-width:1100px){
    .vehicle-tab-main {
        padding-top: 20px;
        display: flex;
        flex-wrap: wrap;       
    }
    .vehicle-tab-left-main {
        width: 95%;
    }
    
    .vehicle-tab-right-main {
        width: 95%;
        padding: 20px 0px 0px 0px;
        height: 80vh;
        overflow: auto;
    }  

    .vehicle-details-box {
        display: grid;
        /* width: 100% !important; */
        padding: 20px;
        /* border: 1px solid #ccc; */
        border-radius: 5px;
        margin-bottom: 20px;
        background-color: var(--glass);
        gap: 10px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    }

    .vehicle-details-section-main {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 17px;
    }
}

.vehicle-details-section-main {
    height: 80vh;
    overflow-y: auto;
}

.vehicle-details-box {
    display: grid;
    width: fit-content;
    padding: 20px;
    /* border: 1px solid #ccc; */
    border-radius: 5px;
    margin-bottom: 20px;
    background-color: var(--glass);
    gap: 10px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.vehicle-details-box-veh-no {
    font-weight: 600;
}

.vehicle-tab-right-top-division-main {
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 10px;
    background-color: var(--glass);
}

.vehicle-tab-right-top-division-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.vehicle-tab-right-top-division-btn-section {
    display: flex;
    gap: 0px 15px;
}

.vehicle-tab-right-top-division-btn-section-2 {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px 15px;
    margin-top: 20px;
    flex-wrap: wrap;
}

.vehicle-stats-main {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    border: 1px solid #ccc;
    padding: 25px;
    border-radius: 10px;
    background-color: var(--glass);
}

.vehicle-stats-box {
    width: 325px;
    display: grid;
    text-align: center;
}

.vehicle-stats-box-border {
    border: 1px solid #ccc;
    padding-right: 0px;
}

.vehicle-stats-box-content {
    display: grid;
    padding: 20px 0px;
}

.vehicle-stats-box-content-border-bottom {
    border-bottom: 1px solid #ccc;
}

.vehicle-stats-box-content-heading {
    font-size: 25px;
    font-weight: 600;
}

.vehicle-stats-distance-box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 20px;
}

.vehicle-stats-distance-box-left {
    width: 21%;
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 10px;
    display: grid;
    text-align: center;
    background-color: var(--glass);
}

.vehicle-stats-distance-box-right {
    width: 70%;
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 10px;
    background-color: var(--glass);
}

@media screen and (max-width:1000px) {
    .vehicle-stats-distance-box-left {
        width: 100%;
        border: 1px solid #ccc;
        padding: 20px;
        border-radius: 10px;
        display: grid;
        text-align: center;
        background-color: var(--glass);
        margin-top: 10px;
    }
    
    .vehicle-stats-distance-box-right {
        width: 100%;
        border: 1px solid #ccc;
        padding: 20px;
        border-radius: 10px;
        background-color: var(--glass);
        margin-top: 10px;

    }


    .vehicle-stats-graph-left {
        width: 100% !important;
        border: 1px solid #ccc;
        padding: 20px;
        border-radius: 10px;
        background-color: var(--glass);
        margin-top: 10px;

    }
    
    .vehicle-stats-graph-right {
        width: 100% !important;
        border: 1px solid #ccc;
        border-radius: 10px;
        padding: 20px;
        display: flex;
        justify-content: center !important;
        gap: 15px;
        text-align: center;
        background-color: var(--glass);
        margin-top: 10px;

    }

    .vehicle-stats-fuel-cost-box {
        border: 1px solid #ccc;
        padding: 20px;
        border-radius: 10px;
        width: 100% !important;
        display: grid;
        text-align: center;
        background-color: var(--glass);
    }
    
    
}

.vehicle-stats-graph-main {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 20px;
}

.vehicle-stats-graph-left {
    width: 61%;
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 10px;
    background-color: var(--glass);
}

.vehicle-stats-graph-right {
    width: 30%;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    gap: 15px;
    text-align: center;
    background-color: var(--glass);
}

.vehicle-stats-fuel-cost-main {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    margin-top: 20px;
}

.vehicle-stats-fuel-cost-box {
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 10px;
    width: 325px;
    display: grid;
    text-align: center;
    background-color: var(--glass);
}

.edit-driver-details-div{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    gap: 15px;
}

.edit-driver-heading{
    width: 200px;
    display: block;
    font-weight: 600;
}

.vehicle-details-box svg{
    font-size: 20px;
}

.dddddddddddd{
    margin-bottom: 20px;
}

