.Scroll-Style-style {
    overflow-y: auto;
  }
  
  .Scroll-Style-style::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    cursor: pointer;
    position: absolute;
  }
  
  .Scroll-Style-style::-webkit-scrollbar-track {
    /* box-shadow: inset 0 0 6px transparent; */
  }
  
  .Scroll-Style-style::-webkit-scrollbar-thumb {
    background-color: #1976d2;
    width: 1px;
    height: 8px;
    cursor: pointer;
    border-radius: 20px;
  }

  .main-content-vehicles {
    zoom: 90%;
  }
  
  @media screen and (min-width: 1300px) and (max-width: 1424.99px) {
    .main-content-vehicles {
      zoom: 82%;
    }
  }
  
  @media screen and (min-width: 1200px) and (max-width: 1299.99px) {
    .main-content-vehicles {
      zoom: 77%;
    }
  }
  