.billings-conatiner {
    color: #fff;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 30px;
    /* align-items: center; */
}

@media screen and (min-width: 769px) and (max-width: 1135px) {
    .billings-conatiner {
        margin-top: 70px;
    }
}

@media screen and (max-width: 768px) {
    .billings-conatiner {
        margin-top: 0px;
    }
}