/* Main Container For User Creation */
.DriverCreation-main {
  width: 100%;
  overflow-x: hidden;
  display: flex;
  color: #000;
}


.DriverCreation-main .DriverCreation-form-container {
  overflow: scroll;
  height: 75vh;
  /* width: 81vw; */
  display: flex;
  flex-direction: column;
  color: #000;
}

@media screen and (max-width: 768px) {
  .DriverCreation-main .DriverCreation-form-container {
    width: 95vw;
  }
}


.DriverCreation-header {
  /* border-top: solid 1px #ccc; */
  /* background: var(--glass); */
  /* padding: 20px 20px 0px 20px; */
  /* margin-bottom: -10px; */
}

.DriverCreation-main .DriverCreation-form-container::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  overflow-x: hidden;
}

.DriverCreation-main .DriverCreation-form-container::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.DriverCreation-main .DriverCreation-form-container::-webkit-scrollbar-thumb {
  background-color: rgb(217, 212, 212);
  outline: 1px solid slategrey;
}

.DriverCreation-table-container {
  /* margin-top: 10px; */
  /* border: 1px solid #ccc; */
  padding: 0px 20px 20px 20px;
  /* background-color: var(--glass); */
}

.table-DriverCreations {
  background: var(--glass);
  border: 2px solid #ccc;
  border-radius: 10px;
  height: 400px;
  width: 100%;
  overflow: hidden;
}

.detail-container-main {
  display: flex;
}

.driver-info-inputs {
  flex-wrap: wrap;
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
}

@media screen and (min-width: 1557px) {
  .driver-info-inputs {
    width: 94%;
  }
}

@media screen and (max-width:1240px) {
  .driver-info-inputs {
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media screen and (min-width:970px) and (max-width:1240px) {

  .side-bar-menu-close .form-container-supplier .container-main {
    width: 96vw;
  }

  .form-container .container-main {
    width: 76vw;
  }
}

@media screen and (min-width:769px) and (max-width:969.99px) {
  .side-bar-menu-close .form-container-supplier .container-main {
    width: 96vw;
  }

  .form-container .container-main {
    width: 70vw;
  }
}

.detail-container-main-crivercreation {
  /* justify-content: center !important; */
