.sidebar {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-top: 4rem;
  padding-right: 10px;
  transition: all 300ms ease;
  border-right: 1px solid #ccc;
  width: -webkit-fill-available;
}

/* .side-bar-menu-close .sidebar{
  padding-left: 10px;
} */

.sidebar-main-menu {
  display: flex;
  align-items: center;
}

.header-user-mobile {
  display: none;
}

/* logo */
/* .bars {
  display: none;
} */

.logo {
  display: flex;
  height: 5rem;
  font-weight: bold;
  font-size: 22px;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  height: 4%;
}

.logo>span>span {
  color: var(--pink);
}

.logo>img {
  width: 6.5rem;
  height: 6.5rem;
}

.side-bar-closed .logo>img {
  width: 50px;
  height: 50px;
}

/* menu */
.menu {
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}



.menuItem {
  display: flex;
  align-items: center;
  gap: 1rem;
  height: 2.5rem;
  margin-left: 2rem;
  position: relative;
  transition: all 300ms ease;
  border-radius: 0.7rem;
  font-size: 18px;
  color: rgb(253, 251, 251);
  font-weight: 600;
  text-decoration: none;
}

.active {
  background: rgba(255, 255, 255, 0.708);
  color: #303030;
  margin-left: 0;
}

.side-bar-closed .active {
  background: rgba(255, 255, 255, 0.708);
  color: #303030;
  margin-left: 0 !important;
}

.side-bar-closed .menuItem {
  gap: 0px;
  margin-left: 18px;
}

.active::before {
  content: "";
  width: 8px;
  height: 100%;
  background: rgb(54, 164, 172);
  color: #000;
  margin-right: calc(1rem - 8px);
}

