.settings-dropdown {
  background: rgba(255, 255, 255, 0.708);
  margin-top: -25px;
  transition: margin-top 1s ease;
}

.settings-dropdown.active {
  margin-top: 0;
}

.settings-dropdown-links {
  transition: transform 0.5s ease;
  display: flex;
}

.side-bar-closed .header-user-mobile{
  display: none;
}

.dropdown-icon {
  display: flex;
  gap: 5px;
  justify-content: center;
  text-decoration: none !important;
  color: #000;
  margin-left: 50px;
  cursor: pointer;
}

.dropdown-links {
  color: #000000 !important;
  text-decoration: none;
  text-align: left;
}

.settings-dropdown-links:hover {
  transform: scale(0.9);
  border-left: 8px solid rgb(54, 164, 172);
  color: #000000
}

.registration-dropdown {
  background-color: rgba(255, 255, 255, 0.708);
  margin-top: -25px;
}

.menu-items-registration {
  text-decoration: none;
}

.closedsidebar {
  left: 60px;
}

@media screen and (max-width: 768px) {
  .closedsidebar {
    left: 20px;
  }
}

.user-icon-update {
  font-size: 19px;
  position: relative;
  display: inline-block;
  margin-right: 10px;
  display: flex;
}

.user-icon-update-dot {

  position: absolute;
  top: -3px;
  right: 0px;
  background-color: red;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  box-shadow: rgba(255, 0, 0, 0.5) 0px 0px 6px 0px;
}

.isRegisterdropdownclicked,
.issettingdropdownclicked,
.ismapdropdownclicked,
.isinfodropdownclicked,
.isbillingdropdownclicked,
.isbookingdropdownclicked {
  transform: scaleY(-1);
}

.sidebar-main-menu-arrow {
  margin-top: 5px;
  padding-left: 10px;
}

.avatar-item-2 {
  display: flex;
  align-items: center;
}

.sidebar-update-img {
  width: 100%;
}

.menu-side-bar-closed {
