@media screen and (max-width: 1300px) {
  .menu-section-scroll{
    height: 85% !important;
    overflow-y: scroll;
  } 
}
.menu-section-scroll{
  /* height: 580px; */
  height: 85%;
  overflow-y: scroll;
}  



.menu-section-scroll::-webkit-scrollbar {
  width: 0px; /* Chrome, Safari, Edge */
}

.menu-section-scroll::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.menu-section-scroll::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
  border: 2px solid #f1f1f1;
}

.menu-section-scroll::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}