/* HEADER CONTAINER */
.TransferDataEntry-page-header {
    border-bottom: solid 1px #ccc;
    padding: 20px;
}

.css-13xfq8m-MuiTabPanel-root {
    display: flex;
    padding: 0px !important;
}

.detail-container-main {
    display: flex;
    padding: 20px;
}

.TransferDataEntry {
    display: flex;
    align-items: center;
    justify-content: center;
}

.TransferDataEntry-form {
    /* height: 100vh; */
    /* width: 100vw; */
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    color: #000;
    /* background-color: var(--glass); */
}


.table-bookingCopy-TransferDataEntry {
    border: 2px solid #ccc;
    border-radius: 10px;
    background-color: var(--glass);
    /* margin: 0px 20px; */
    /* width: 100%; */
}

.Download-btn-transferdata {
    /* padding-bottom: 20px; */
}

.copy-title-btn-TransferDataEntry {
    width: 100%;
    /* margin-top: 15px; */
    background-color: var(--glass);
    padding: 10px 20px 35px;
    border: 2px solid #ccc;
    border-radius: 10px;
    /* margin-left: 20px; */
}

.TransferDataEntry-Box {
    border: 2px solid #ccc;
    background-color: var(--glass);
    border-radius: 5px;
}

.total-container-TransferDataEntry {
    display: flex;
    align-items: center;
    /* justify-content: center; */
    flex-wrap: wrap;
    /* padding: 10px 25px; */
}

.total-container-TransferDataEntry .amount-calculator {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    padding: 10px;
    border-radius: 10px;
}

.total-container-TransferDataEntry .total-inputs {
    display: flex;
    flex-direction: row;
    white-space: nowrap;
    padding: 0px 10px;
    align-items: center;
}

.total-container-TransferDataEntry .total-inputs label {
    padding: 0px 10px;
    font-weight: 500;
}

.total-container-TransferDataEntry .total-inputs input {
    background-color: transparent;
    border: 1.5px solid #ccc;
    border-radius: 5px;
    padding: 5px;
}

