.input-feild-transferdata {
    flex-wrap: wrap;
}

@media screen and (max-width:1240px) {
    .input-feild-transferdata {
        justify-content: center;
    }
}

@media screen and (min-width:379.99px) and (max-width:700px) {
    .copy-title-btn-TransferDataEntry {
        width: 80vw;
    }
}

@media screen and (min-width: 320px) and (max-width: 380px) {
    form .detail-container-main-transfer-data {
        flex-wrap: wrap !important;
        display: flex !important;
        width: 95% !important;
        justify-content: center !important;
    }

    .copy-title-btn-TransferDataEntry {
        width: 90%;
        margin-top: 30px;
        background: none;
        padding: 10px 20px 35px;
        border: none;
        border-radius: 10px;
    }
}

.transfer-data-entry-table {
    height: 400px;
    width: 100%;
}