.total-container {
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 0px 25px; */
    flex-wrap: wrap;
    gap: 10px;
}

.total-container .amount-calculator {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-radius: 10px;
    flex-wrap: wrap;
    gap: 10px;
}

.total-container .total-inputs {
    display: flex;
    align-items: center;
}

.total-container .total-inputs label {
    padding: 0px 10px;
    font-weight: 500;
    white-space: nowrap;
    font-size: 14px;
}

.total-container .total-inputs input {
    background-color: transparent;
    border: 1.5px solid #ccc;
    border-radius: 5px;
    padding: 5px;
}

.input-field-payment {
    flex-wrap: wrap;
    align-items: center;
    /* margin-bottom: 20px; */
}

